<template>
  <v-layout column class="popup-root">
    <v-layout class="lay-title px-6 pt-6" align-center justify-space-between>
      <img
        src="@/assets/images/ic-back.svg"
        @click.prevent.stop="
          () => {
            onClose ? onClose() : null;
          }
        "
      />
      <span>카메라 셋팅하기</span>
      <div />
    </v-layout>
    <v-layout column class="px-6 pt-6 pb-8 lay-border-btm">
      <v-layout class="lay-sub-title" align-center justify-space-between>
        <span>High</span>
        <v-switch
          :ripple="false"
          hide-details
          color="#15D4E0"
          v-model="high"
          class="mr-n4 btn-switch"
          inset
          flat
          dense
        />
      </v-layout>
      <span class="txt-sub">해상도</span>
      <v-layout class="lay-btn">
        <c-btn
          :height="38"
          :fontSize="15"
          :py="8"
          :thema="isUltra ? 'default' : 'disable'"
          class="mr-4"
          :onClick="() => (isUltra = true)"
          >Ultra Wide</c-btn
        >
        <c-btn
          :height="38"
          :fontSize="15"
          :py="8"
          :thema="!isUltra ? 'default' : 'disable'"
          :onClick="() => (isUltra = false)"
          >Wide</c-btn
        >
      </v-layout>
    </v-layout>
    <v-layout column class="px-6 pt-6 pb-8">
      <v-layout class="lay-sub-title" align-center justify-space-between>
        <span>화면 비율</span>
      </v-layout>
      <v-layout class="lay-btn pt-2">
        <c-btn
          v-for="(item, index) in ratios"
          :key="index"
          :height="48"
          :fontSize="15"
          :py="8"
          :thema="ratio == index ? 'default' : 'disable'"
          :class="[index < ratios.length - 1 ? 'mr-1' : '']"
          :onClick="() => (ratio = index)"
        >
          <v-layout class="txt-btn" column
            >{{ item.value }}
            <span v-if="item.sub" class="txt-btn-sub pt-1">{{ item.sub }}</span>
          </v-layout>
        </c-btn>
      </v-layout>
    </v-layout>
  </v-layout>
</template>

<script>
export default {
  props: {
    onClose: {
      default: () => {},
    },
  },
  data() {
    return {
      high: null,
      isUltra: null,
      ratio: null,
      ratios: [
        {
          value: "16:10",
          sub: "Full Screen",
        },
        {
          value: "16:9",
          sub: null,
        },
        {
          value: "4:3",
          sub: "최고화질",
        },
      ],
    };
  },
  computed: {},
  watch: {
    high() {
      if (this.high != null) {
        this.nativeBridge.setPrefData("PREF_CAMERA_SETTING_HIGH", this.high);
      }
    },
    isUltra() {
      if (this.isUltra != null) {
        this.nativeBridge.setPrefData("PREF_CAMERA_SETTING_ULTRA", this.isUltra);
      }
    },
    ratio() {
      if (this.ratio != null) {
        this.nativeBridge.setPrefData(
          "PREF_CAMERA_SETTING_RATIO",
          this.ratios[this.ratio].value
        );
      }
    },
  },
  created() {
    this.nativeBridge.getPrefData("PREF_CAMERA_SETTING_HIGH", "onGetPrefData");
    this.nativeBridge.getPrefData("PREF_CAMERA_SETTING_ULTRA", "onGetPrefData");
    this.nativeBridge.getPrefData("PREF_CAMERA_SETTING_RATIO", "onGetPrefData");
    window.onGetPrefData = (key, value) => {
      switch (key) {
        case "PREF_CAMERA_SETTING_HIGH":
          this.high = value ? JSON.parse(value) : false;
          break;
        case "PREF_CAMERA_SETTING_ULTRA":
          this.isUltra = value ?  JSON.parse(value) : false;
          break;
        case "PREF_CAMERA_SETTING_RATIO":
          var ra =  value ? value : "16:9";
          this.ratios.some((element, index) => {
            if (element.value == ra) {
              this.ratio = index;
              return true;
            }
          });
          break;
      }
    };
  },
  mounted() {},
  destroyed() {
    delete window.onGetPrefData;
  },
  methods: {},
};
</script>

